.resume-container {
  min-width: 100%;
  width: calc((25em - 100%) * 1000);
  max-width: 100%;
  /* Change 25em to your breakpoint. */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  .header {
    width: 100%;
    padding: 2% 5% 2% 5%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    .img-container {
      max-width: 200px;
      max-height: 200px;
      border-radius: 50%;
      // transition: .5s ease-in-out;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        object-fit: cover;
        animation: img-anim 1.5s ease-in-out;
      }
    }
    .text-container {
      display: flex;
      flex-direction: column;
      // justify-content: center;
      padding: 0 5% 0 2%;
   
      .text-header {
        font-size: 32px;
        line-height: 50px;
        color: black;
        animation: text-anim 1s ease-in-out;
      }
      .short-description {
        color: darkcyan;
        font-size: 26px;
        line-height: 40px;
        animation: text-anim 1s ease-in-out;
      }
      .description {
        padding: 2% 0px 0px 0px;
        animation: text-anim 1s ease-in-out;
      }
    }
  }
  .contact-details {
    width: 100%;
    color: white;
    background-color: darkcyan;
    display: flex;
    flex-direction: row;
    padding: 0px 5% 0px 5%;
    justify-content: space-around;
    transition: all 1s ease-in-out;
    animation: contact-anim 1s ease-in;
    .col {
      .item {
        padding: 14px;
        a {
          text-decoration: none;
          color: white;
        }
       
      }
    }
  }
  .detail-info{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    .experience{

    }
    .skills{

    }
  }
}


/* animations */

@keyframes img-anim{
0%{
  transform:translateY(100%)
}
100%{
  transform:translateY(0%)
}
}
@keyframes text-anim{
  0%{
    transform: translate(250%,0%);
    // width: 0px;
    
  }
  50%{
    transform: skewX(30deg);
  }
  100%{
    transform: translate(0%,0%);
    // height: 200px;
    // width: 200px;
  }
  }

  @keyframes contact-anim{
    0%{
      height:1px;
    }
    50%{
      height:100px;
    }
    75%{
      height:150px;
    }
    100%{
      height: auto;
    }
    }
  