.test{
  position: absolute;
  height: 5000px;
  bottom: 0;
}
h2{

  position: fixed;
}
.App {
  
    text-align: center;
    background-color: #282c34;
    height: 100vh;
    overflow: overlay;
    position: absolute;
    width: 100%;
    .ins{
      width: 100%;
      justify-content: center;
      align-items: center;
      color: turquoise;
      // position: absolute;
      top:10px;
      font-weight: 500;
      font-size: larger;

    }
  }
  
  .App-logo {
    height: 40vmin;
    /* pointer-events: none; */
    border-radius: 400px;
  }
  
  .App-logo:hover {
    height: 41vmin;
    // border-radius: 400px;
    // background-image: url('https://i.pinimg.com/736x/46/92/cd/4692cdb7d1166b63fdef407b478d7d04.jpg');
  }
  
  @keyframes flip {
    0% {
      opacity: 1;
    }
    25% {
      opacity: 0;
    }
    50% {
      opacity: 1;
      content: "Developer"
    }
    65% {
      opacity: 0;
    }
    80% {
      opacity: 1;
    }
    90% {
      opacity: 0;
    }
    100% {
      opacity: 1;
      content: "Cinephile"
    }
  }
  
  .toBeReplaced:before {
    content: 'Photography enthusiast';
    animation-name: flip;
    animation-duration: 6s;
    animation-iteration-count: infinite;
  }
   

  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: float 6s ease-in-out infinite;
    }
    .icons {
      animation: float 6s ease-in-out infinite;
    }
  }
  
  @keyframes float {
    0% {
      box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
      transform: translatey(0px);
    }
    50% {
      box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
      transform: translatey(-20px);
    }
    100% {
      box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
      transform: translatey(0px);
    }
  }
  
  .skill-icons {
    display: flex;
    flex-direction: row;
  }
  
  .skill,.skill-main {
    margin: 2%;
   
  }
  .skill img {
    width: 40px;
    height: 40px;
  }
  .skill-main img{
    width: 80px;
    height: 50px;
  }
  .App-header {
    background-color: #282c34;
    // min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    transition: all 1s ease;
    height: 100vh;
    // opacity: 0;
    /* border-radius: 900px; */
    // transform: translatex(-150%) !important;

  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    25% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(25deg);
    }
    75% {
      transform: rotate(-25deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  @media screen and (max-width: 600px) {
    .App-logo {
      height: 80vmin;
      /* pointer-events: none; */
      border-radius: 400px;
    }
    
    .entry-buttons {
      display: flex;
      flex-direction: column!important;

      color: rgb(228, 210, 207);
      width: 100%;
      margin-bottom: 50px;
    }
    .App-header {
      background-color: #282c34;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      /* align-items: center; */
      justify-content: center;
      font-size: calc(10px + 2vmin);
      color: white;
      transition: all 1s ease;
      /* border-radius: 90px; */
      /* margin: 5%; */
    }
    .icon {
      top: 25%!important;
      ;
      left: -30px!important;
      width: 30px;
      height: 30px;
      // position: absolute;
      z-index: 2;
      /* border-radius: 10%px; */
    }
    .icon-code {
      animation: rotateSmall 4s linear infinite;
    }
    .icon-movie {
      animation: rotateSmall 3s linear infinite;
    }
    .icon-cam {
      animation: rotateSmall 2s linear infinite;
    }
    .icon-movie:before {
      position: absolute;
      height: 0px;
      width: 0px;
      animation: rotateSmall 4s linear reverse infinite;
      z-index: 2;
    }
    .icon-code:before {
      position: absolute;
      height: 0px;
      width: 0px;
      animation: rotateSmall 6s linear reverse infinite;
      z-index: 2;
    }
    .icon-cam:before {
      position: absolute;
      height: 0px;
      width: 0px;
      animation: rotateSmall 2s linear reverse infinite;
      z-index: 2;
    }
    @keyframes rotateSmall {
      0% {
        width: 10px;
        height: 20px;
        transform: translatex(10px);
      }
      10% {
        width: 20px;
        height: 20px;
        transform: translatex(10px);
      }
      20% {
        width: 30px;
        height: 30px;
        transform: translatex(15px);
      }
      50% {
        width: 30px;
        height: 30px;
        transform: translatex(20px);
      }
      70% {
        width: 30px;
        height: 30px;
        transform: translatex(23px);
      }
      80% {
        width: 20px;
        height: 20px;
        transform: translatex(25px);
      }
      90% {
        width: 20px;
        height: 20px;
        transform: translatex(35px);
      }
      100% {
        width: 10px;
        height: 10px;
        transform: translatex(35px);
      }
    }
  }
  
  .icon {
    color: rgba(0, 0, 0, 0.726);
    top: 15%;
    left: 35%;
    width: 100px;
    height: 100px;
    transform-origin: 200% center;
    position: absolute;
    z-index: 2;
    border-radius: 10%;
  }
  
  .icon-code {
    animation: rotateBig 6s linear infinite;
  }
  
  .icon-movie {
    animation: rotateBig 4s linear infinite;
  }
  
  .icon-cam {
    animation: rotateBig 2s linear infinite;
  }
  
  .icon-movie:before {
    position: absolute;
    height: 0px;
    width: 0px;
    animation: rotateBig 4s linear reverse infinite;
    z-index: 2;
  }
  
  .icon-code:before {
    position: absolute;
    height: 0px;
    width: 0px;
    animation: rotateBig 6s linear reverse infinite;
    z-index: 2;
  }
  
  .icon-cam:before {
    position: absolute;
    height: 0px;
    width: 0px;
    animation: rotateBig 2s linear reverse infinite;
    z-index: 2;
  }
  
  @keyframes rotateBig {
    0% {
      width: 10px;
      height: 20px;
      transform: translatex(100px);
    }
    10% {
      width: 50px;
      height: 50px;
      transform: translatex(150px);
    }
    20% {
      width: 60px;
      height: 60px;
      transform: translatex(150px);
    }
    50% {
      width: 70px;
      height: 70px;
      transform: translatex(200px);
    }
    70% {
      width: 60px;
      height: 60px;
      transform: translatex(230px);
    }
    80% {
      width: 50px;
      height: 50px;
      transform: translatex(250px);
    }
    90% {
      width: 30px;
      height: 30px;
      transform: translatex(280px);
    }
    100% {
      width: 10px;
      height: 10px;
      transform: translatex(290px);
    }
  }


  body {
    font-family: 'Press Start 2P', cursive;
    font-size: 16px;
    font-weight: bold;
    color: #a5a5ff;
  }

  .entry-buttons{
   
    transition: all 4s 1s;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

    bottom: 0;
    width: 100%;
    height: 100vh;

    img{
      width:200px;
      height: 200px;
      &:hover{
        transform: scale(1.1);
        transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      }
    }
    .options{
      cursor: pointer;
      &:hover{
        transform: scale(1.1);
        transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      }
     
    }
 }


 .item{
  // transform: translatex(-150%) !important;
  transition: all 1s ease-in-out;
  width: 100%;
  // opacity: 0;

  // max-height: 600px;
  height: auto;
  // position: absolute;
 }




 .revealer {
  position: relative;
}

.revealme {
  transition: all 1s ease;
  opacity: 0;
}

.active1 .revealme {
  opacity: 1;
}

.revealme.fromleft {
  transform: translateX(-150%);
}

.active1 .revealme.fromleft {
  opacity: 1;
   transform: translateX(0%);
  
}

.revealme.fromright {
  transform: translateX(150%);
}

.active1 .revealme.fromright {
  opacity: 1;
  transform: translateX(0%);
}

.profile{
  max-width: 401px;
  width: 500px;
}

.icon-group{
position: absolute;
}

